* {
  box-sizing: border-box;
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Inter', 'Arial', sans-serif;
  font-style: normal;
  font-weight: 400;
  min-width: 375px;
}

.container {
  max-width: 1230px;
  min-width: 375px;
  margin: 0 auto;
  padding: 0 15px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
}

.header__top-logo {
  text-decoration: none;
  font-weight: 800;
  font-size: 22px;
  line-height: 25px;
  color: #3E416D;
}

.header__top-logo:hover {
  color: #8482FF;
}

.header__top-logo-color {
  color: #8482FF;
}

.header__top-menu-link {
  text-decoration: none;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  color: #3E416D;
  padding: 0 15px;
}

.header__top-menu-link:hover {
  color: #8482FF;
  text-decoration: underline;
}

.header__top-basket {
  text-decoration: none;
}

.header__top-basket:hover .header__top-basket-svg path {
  fill: #8482FF;
}

@media (max-width: 900px) {
  .header__top {
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: auto auto;
  }
  .header__top-menu {
    grid-column: 1 / 3;
    grid-row: 2 / -1;
    text-align: center;
    padding-top: 30px;
  }
  .header__top-basket {
    grid-column: 2 / 3;
    grid-row: 1 / 2;
    text-align: right;
    padding-right: 10px;
  }
}

@media (max-width: 700px) {
  .header__top-menu {
    display: grid;
    grid-template-columns: auto auto auto;
    grid-template-rows: auto auto;
  }
}

@media (max-width: 390px) {
  .header__top-menu {
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: auto auto auto auto auto;
  }
  .header__top-menu-link {
    font-size: 22px;
    margin: 15px 0;
  }
}

.main__intro {
  max-height: 100%;
  overflow: hidden;
  position: relative;
}

.main__intro::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
}

.main__intro-background {
  position: relative;
  padding-bottom: 46.1%;
  z-index: 1;
}

.main__intro-background-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
}

.main__intro-content {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding-top: 50px;
}

.main__intro-content-title {
  font-weight: 700;
  font-size: 60px;
  line-height: 73px;
  margin: 0 auto;
  color: #FFFFFF;
  max-width: 780px;
}

.main__intro-content-title.mobile {
  display: none;
}

.main__intro-content-btn {
  text-align: center;
}

.main__intro-content-btn-link {
  text-decoration: none;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  color: #FFFFFF;
  background: transparent;
  border-radius: 4px;
  padding: 15px 23px;
  margin: 0 15px;
  border: 2px solid #FFFFFF;
}

.main__intro-content-btn-link.general {
  background: #8482FF;
}

.main__intro-content-btn-link.general:hover {
  background-color: #615efd;
}

.main__intro-content-btn-link:hover {
  background: #8482FF;
  transition: 1s;
}

.main__intro-content-down {
  text-align: center;
  transition: transform 1s linear;
}

.main__intro-content-down:hover {
  transform: translateY(30px);
}

.main__intro-content-down:hover .main__intro-content-down-svg path {
  stroke: white;
  opacity: 1;
  transition: 1s;
}

@media (max-width: 991px) {
  .main__intro-content {
    padding-top: 0;
  }
  .main__intro-content-down {
    display: none;
  }
  .main__intro-content-title {
    font-size: 40px;
    max-width: 600px;
    text-align: center;
  }
}

@media (max-width: 679px) {
  .main__intro-content-title {
    display: none;
  }
  .main__intro-content-title.mobile {
    display: block;
  }
  .main__intro-content-btn {
    padding-bottom: 30px;
  }
}

.main__cources {
  margin-top: 100px;
}

.main__cources-title {
  font-weight: bold;
  font-size: 40px;
  line-height: 48px;
  text-align: center;
  color: #3E416D;
}

.main__cources-examples {
  padding-top: 60px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  row-gap: 40px;
  column-gap: 40px;
}

.main__cources-examples-block {
  background: #E7E7FF;
  border-radius: 10px;
  padding: 17px 25px;
}

.main__cources-examples-block-purple {
  width: 82px;
  height: 60px;
  background: #8482FF;
  border-radius: 10px;
  margin-bottom: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 40px;
  line-height: 48px;
  color: #FFFFFF;
}

.main__cources-examples-block-text {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #3E416D;
  opacity: 0.8;
}

@media (max-width: 854px) {
  .main__cources-examples {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
  }
}

@media (max-width: 564px) {
  .main__cources-examples {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
  }
}

.main__advantages {
  padding: 150px 0;
}

.main__advantages-title {
  margin: 0 auto;
  font-weight: bold;
  font-size: 40px;
  line-height: 48px;
  text-align: center;
  color: #3E416D;
  max-width: 477px;
  margin-bottom: 80px;
}

.main__advantages-examples {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  column-gap: 35px;
  row-gap: 47px;
}

.main__advantages-examples-block {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 38px 56px;
  background: #FFFFFF;
  border: 1px solid rgba(62, 65, 109, 0.07);
  border-radius: 10px;
  filter: drop-shadow(0px 7px 17px rgba(132, 130, 255, 0.1));
}

.main__advantages-examples-block-title {
  max-width: 179px;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  color: #3E416D;
  padding-top: 18px;
  text-align: center;
}

.main__advantages-examples-block-line {
  width: 45px;
  height: 4px;
  background: #8482FF;
  margin: 15px 0 10px;
}

.main__advantages-examples-block-text {
  max-width: 256px;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #3E416D;
  opacity: 0.8;
  text-align: center;
}

@media (max-width: 854px) {
  .main__advantages-examples {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
  }
}

@media (max-width: 564px) {
  .main__advantages-examples {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
  }
}
